@import "../lib/DINMITTELSCHRIFT";

.border-box {
    box-sizing: border-box;
}
.num {
    font-family: 'Dinschrift', 'Roboto', 'Helvetica', sans-serif;
    -webkit-tap-highlight-color: transparent!important;
}
.pr {
    position: relative;
}
.pa {
    position: absolute;
}
.ell {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.fl {
    float: left;
}
.fr {
    float: right;
}
.fb {
    font-weight: bold;
}
.tc {
    text-align: center;
}

// 垂直居中
.fccc {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

// 水平居中
.frcc {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

// 文字溢出
.overflow {
    word-wrap: normal;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
