.mt20 {
  margin-top: 20px;
}
.fw6 {
  font-weight: 600;
}
.index {
  width: 100%;
  height: 100%;
  color: #222;
  font-size: 28px;
  justify-content: flex-start !important;
  overflow-x: hidden;

  &_cont {
    width: 660px;
    background-color: #ffffff;
    box-shadow: 0px 10px 30px 0px rgba(209, 235, 254, 0.7);
    border-radius: 6px;
    text-align: left;
    padding: 40px 27px 25px;
    box-sizing: border-box;
    &-title1 {
      font-size: 44px;
    }
    &-title2 {
      font-size: 22px;
      color: #999;
      margin-top: 20px;
      margin-bottom: 20px;
      i {
        @include bg('diam-s', 24, 19);
        display: inline-block;
      }
    }
    &-no {
      color: #999;
      text-align: center;
      font-size: 22px;
      margin: 45px auto;
    }
    &-award {
      &--item {
        width: 100%;
        justify-content: space-between !important;
        margin-bottom: 20px;
      }
      &--img {
        width: 100px;
        height: 100px;
      }
      &--cont {
        flex: 1;
        text-align: left;
        padding: 0 20px;
      }
      &--title {
        font-size: 26px;
      }
      &--tip {
        font-size: 22px;
        color: #999;
        margin-top: 8px;
        overflow : hidden;
        text-overflow: ellipsis;
        display:  -webkit-inline-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
      }
      &--btn {
        min-width: 120px;
        height: 64px;
        text-align: center;
        line-height: 64px;
        background-color: #ff4a6d;
        border-radius: 6px;
        color: #fff;
        padding: 0 20px;
        box-sizing: border-box;
      }
      &--btndone {
        background-color: #bbb !important;
      }
    }
  }

}
