
























@import '~@/css/lib/_mixin';
.index {
  &_header {
    justify-content: space-between !important;
    z-index: 9;
    box-sizing: border-box;
    @include bg('banner',  720, 321);
    width: 100% !important;
    margin-bottom: 95px;

    &-title--en {
      @include bg('title-en', 659, 104);
      margin: 80px auto 0;
    }
    &-title--id {
      @include bg('title-id', 701, 114);
      margin: 70px auto 0;
    }
    &-title--hi {
      @include bg('title-hi', 694, 160);
      margin: 40px auto -20px;
    }
    &-alert {
      @include bg('bg', 696, 236);
      margin: 0 auto;
      justify-content: flex-start !important;
      padding-left: 50px;
      padding-bottom: 15px;
      box-sizing: border-box;
      width: auto;
      max-width: 696px;
      &--left {
        font-size: 94px;
        color: #ffdf42;
        img {
          width: 50px;
          height: 39px;
          margin-right: 5px;
        }
      }
      &--right {
        color: #fff;
        margin-left: 35px;
        text-align: left;
        div {
          font-size: 34px;
        }
        p {
          font-size: 22px;
          margin-top: 15px;
        }
      }
    }
  }
}

