




























@import '~@/css/lib/_mixin';

.ani {
  animation: giftShake 0.5s 0.4s;
  animation-fill-mode: forwards;
}

@keyframes giftShake {
  0% {
    transform: scale(0.2);
  }
  25% {
    transform: scale(0.8);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}

.info {
  padding-top: 375px;
  color: #000;
  box-sizing: border-box;
  justify-content: space-between !important;
  @include bg('modal', 550, 773);
  &_title {
    font-size: 36px;
    font-weight: 600;
    color: #fff;
  }
  &_close {
    @include bg('close', 22, 22);
    right: 24px;
    top: 154px;
  }
  &_score {
    top: 0;
    left: 0;
    width: 100%;
    color: #ffce39;
    font-size: 107px;
    transform: scale(0);
  }
  &_tip1 {
    margin-top: 47px;
    span {
      color: #ffc215;
    }
  }
  &_tip2 {
    margin-top: 22px;
    span {
      color: #ffc215;
    }
  }
  &_line {
    margin: 20px auto 0;
    width: 440px;
    height: 1PX;
    background: #eee;
  }
  &_tip3 {
    margin: 25px auto 0;
    color: #999;
    font-size: 22px;
    width: 450px;
  }
  &_btn {
    margin: 25px auto 0;
    width: 350px;
    height: 70px;
    line-height: 70px;
    background-color: #ffc215;
    border-radius: 6px;
    color: #fff;
  }
}
