@mixin bg($url, $width, $height, $type:"") {
    background-image: url('~@/img/' + $url + if($type!="",$type,'.png'));
    background-size: 100% 100%;
    width: $width + px;
    height: $height + px;
}

@mixin text-overflow() {
    word-wrap: normal;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
