@import "lib/mixin";
@import "lib/normalize";

@import "common/base";
@import "common/acss";

html, body {
    background: #fff;
    color: #000;
    width: 100%;
    height: 100%;
    font-family: -apple-system, 'Roboto', 'Helvetica Neue', 'Helvetica', 'Arial',
      'PingFang SC', sans-serif;
    // 去掉移动端点击高亮
    -webkit-tap-highlight-color: transparent;
}

div[marginLeft="ar"], p[marginLeft="ar"], input[marginLeft="ar"], span[marginLeft="ar"],
div[position="ar"], p[position="ar"], input[position="ar"], span[position="ar"] {
    transform: scaleX(-1);
    > * {
        transform: scaleX(-1);
        display: inline-block;
    }
}

div[textLeft="ar"], p[textLeft="ar"], input[textLeft="ar"], span[textLeft="ar"], textarea[textLeft="ar"] {
    text-align: right !important;
}
